.button {
   padding: 8px 24px;
   border: none;
   border-radius: 3px;
   color: #ffffff;
   font-size: 14px;
   font-weight: 700;
}

.disabled {
   background-color: #004D99;
   opacity: 0.5;
   color: #002E5C;
}

.secondary {
   background-color: #ffffff;
   border: 1px solid #0066CC;
   color: #0066CC;
}

.default {
   background-color: #0066CC;
}

.red {
   background-color: #CC1439;
}

.default:hover {
   cursor: pointer;
   background-color: #0080FF;
}

.red:hover {
   cursor: pointer;
   background-color: #FF1A47;
}

.secondary:hover {
   cursor: pointer;
   background-color: #E5F0FA;
}


.default:active {
   background-color: #0059B2;
}

.secondary:active {
   background-color: #C9E5FF;
}

.red:active {
   background-color: #B21232;
}

.button:disabled {
   background-color: #004D99;
   opacity: 0.5;
}
